import {LoadingController} from 'ionic-angular';
import { Component } from "@angular/core";
import {
  IonicPage,
  NavController,
  NavParams,
  ViewController
} from "ionic-angular";
import { FileBrowserViewModel } from '../../Model/ViewModel/FileBrowserViewModel';

/**
 * Generated class for the DirectoryBrowserModalPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: "page-file-browser-copy-edit-modal",
  templateUrl: "file-browser-copy-edit-modal.html"
})
export class FileBrowserCopyEditModalPage {
  item: FileBrowserViewModel;
  mode: string;
  CanEditFileBrowser: boolean;
  title:string;
   constructor(
    public navCtrl: NavController,
    public navParams: NavParams,
    public viewCtrl: ViewController,
    public loadingCtrl: LoadingController,
  ) {

    this.item = new FileBrowserViewModel();
    this.item.filepath = navParams.data.item.filepath;
    this.item.filefullname= navParams.data.item.filefullname;
    this.item.filename = navParams.data.item.filename;
    this.item.rename_filename = navParams.data.item.filename+"_copy";
    this.item.create_time = navParams.data.item.create_time;

    this.CanEditFileBrowser = navParams.data.CanEditFileBrowser;
    this.mode = navParams.data.mode;
    if(this.mode==="Rename")
      this.title="變更檔案";
    else
    this.title="複製檔案";
  }

  Save() {
    this.viewCtrl.dismiss(this.item);
  }
  close() {
    this.viewCtrl.dismiss();
  }
  ionViewDidLoad() {
    console.log("ionViewDidLoad DirectoryBrowserModalPage");
  }
}



